import VueECharts from 'vue-echarts'

// 排行
export function rankingBar({ names = [], counts = [] } = {}) {
  return {
    tooltip: {
      trigger: 'axis',
      confine: true,
      axisPointer: {
        type: 'none'
      },
      formatter: '{b}<br/>数量：{c}'
    },
    grid: {
      top: 20,
      left: '3%',
      right: '8%',
      bottom: 0,
      containLabel: true
    },
    graphic: {
      type: 'text',
      // 相对父元素居中
      left: 'center',
      // 相对父元素居中
      top: 'middle',
      style: {
        fill: '#fff',
        text: names.length === 0 ? '无预警' : '',
        font: '14px Microsoft YaHei'
      }
    },
    xAxis: [{
        show: false
      },
      {
        show: false
      }
    ],
    yAxis: {
      type: 'category',
      inverse: true,
      axisLine: {
        show: false
      },
      axisTick: {
        show: false
      },
      axisLabel: {
        color: '#fff',
        interval: 0,
        fontSize: 10,
        formatter: function (item, index) {
          if (index < 3) {
            return ['{top3|', index + 1, '}'].join(' ')
          } else {
            return ['{top7|', index + 1, '}'].join(' ')
          }
        },
        rich: {
          top3: {
            color: 'rgb(255, 161, 0)',
          },
          top7: {
            color: 'rgb(0, 151, 136)'
          }
        }
      },
      data: names
    },
    series: [{
        type: 'bar',
        barWidth: '20%',
        label: {
          show: true,
          position: 'right',
          textStyle: {
            color: '#fff'
          }
        },
        itemStyle: {
          color: {
            type: 'linear',
            x: 0,
            y: 0,
            x2: 1,
            y2: 0,
            colorStops: [{
              offset: 0,
              color: 'rgba(255, 56, 106, .5)'
            }, {
              offset: 1,
              color: 'rgba(255, 56, 106, .8)'
            }],
            globalCoord: false
          }
        },
        data: counts
      },
      {
        type: 'bar',
        xAxisIndex: 1,
        barWidth: '20%',
        barGap: '-100%',
        itemStyle: {
          color: 'transparent'
        },
        label: {
          show: true,
          color: '#fff',
          fontSize: 10,
          position: [0, -15],
          formatter: data => names[data.dataIndex]
        },
        data: counts
      }
    ]
  }
}

// 单柱状图（排名）
export function singleBar({ names = [], counts = [] } = {}) {
  return {
    tooltip: {
      trigger: 'axis',
      confine: true,
      axisPointer: {
        type: 'shadow'
      }
    },
    grid: {
      top: 20,
      left: '3%',
      right: '8%',
      bottom: 0,
      containLabel: true
    },
    xAxis: {
      show: false
    },
    yAxis: {
      type: 'category',
      inverse: true,
      axisLine: {
        show: false
      },
      axisTick: {
        show: false
      },
      axisLabel: {
        color: '#fff',
        interval: 0,
        fontSize: 10,
        formatter: function (item, index) {
          if (index < 3) {
            return ['{top3|', index + 1, '}'].join(' ')
          } else {
            return ['{top7|', index + 1, '}'].join(' ')
          }
        },
        rich: {
          top3: {
            color: '#fff',
            backgroundColor: 'rgb(255, 161, 0)',
            width: 16,
            height: 16,
            align: 'center',
            borderRadius: 8
          },
          top7: {
            color: '#fff',
            backgroundColor: 'rgb(0, 151, 136)',
            width: 16,
            height: 16,
            align: 'center',
            borderRadius: 8
          }
        }
      },
      zlevel: 1,
      data: names
    },
    graphic: {
      type: 'text',
      // 相对父元素居中
      left: 'center',
      // 相对父元素居中
      top: 'middle',
      style: {
        fill: '#fff',
        text: names.length === 0 ? '无预警' : '',
        font: '14px Microsoft YaHei'
      }
    },
    series: [{
      name: '数量',
      type: 'bar',
      barWidth: 12,
      label: {
        show: true,
        position: 'insideLeft',
        formatter: function (item) {
          return ['{left|' + item.name + '}', '{right|' + item.value + '}'].join(' ')
        },
        rich: {
          left: {
            color: '#fff',
            fontSize: 10
          },
          right: {
            color: '#fff',
            fontSize: 10
          }
        }
      },
      itemStyle: {
        color: {
          type: 'linear',
          x: 0,
          y: 0,
          x2: 1,
          y2: 0,
          colorStops: [{
            offset: 0,
            color: 'rgba(255, 56, 106, 1)'
          }, {
            offset: 1,
            color: 'rgba(255, 56, 106, .8)'
          }],
          globalCoord: false
        }
      },
      data: counts
    }]
  }
}

